@keyframes blink {
  0%,
  10%,
  50%,
  100% {
    opacity: 1; }
  25%,
  35%,
  75% {
    opacity: 0; } }

html {
  box-sizing: border-box;
  font-family: "Fira Code", monospace; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  color: #FFD428;
  background: #000; }

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .main__header {
    text-align: center;
    margin-top: 60px; }

.table-wrapper {
  display: block;
  width: 90%;
  overflow-x: auto; }

.table {
  text-align: center;
  border-spacing: 0;
  width: 100%; }
  .table__head {
    width: 100%; }
  .table__head-tr {
    height: 50px; }
  .table__head-th {
    border: 1px solid #fff;
    color: #fff;
    text-decoration: underline;
    font-weight: 700;
    padding: 0 10px; }
  .table__body-row {
    height: 50px; }
    .table__body-row:hover {
      color: #fff;
      transform: scale(1.02);
      cursor: pointer; }
    .table__body-row--edited {
      animation: blink 1s; }
  .table__body-td {
    border: 1px solid #fff;
    padding: 0 10px; }

.edit-form {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  min-width: 20vw;
  text-align: center;
  margin-bottom: 40px; }
  .edit-form__header {
    font-size: 20px;
    margin-bottom: 40px;
    position: relative; }
    .edit-form__header:after {
      position: absolute;
      display: block;
      font-size: 12px;
      right: 0;
      bottom: -20px;
      color: #fff;
      content: "⃰Choose the row to edit data"; }
      @media (max-width: 640px) {
        .edit-form__header:after {
          left: 50%;
          bottom: -30px;
          transform: translate(-50%); } }

.form {
  width: 100%;
  border: 1px solid #fff;
  padding-bottom: 10px; }
  .form--blurred {
    filter: blur(2px); }
  .form__element {
    display: flex;
    justify-content: space-between;
    color: #fff;
    padding: 0 20px; }
    .form__element--inactive {
      pointer-events: none; }
  .form__label {
    text-align: left;
    margin-right: 10px; }
  .form__btn-submit {
    width: 140px;
    height: 30px;
    background-color: #FFD428;
    border: none;
    cursor: pointer; }
